<template>
	<div class="google-map"></div>
</template>

<script>
import gmapsInit from '@/utils/gmaps';
import map_icon_origin from '../assets/images/map-icon-origin.png';
import map_icon_numbered_1 from '../assets/images/map-icon-numbered-1.png';
import map_icon_numbered_2 from '../assets/images/map-icon-numbered-2.png';
import map_icon_numbered_3 from '../assets/images/map-icon-numbered-3.png';
import map_icon_destination from '../assets/images/map-icon-destination.png';

export default {
	name: 'GoogleMap',
	props: {

	},
	async mounted() {
		try {
			const journey = [
				{
					station: 'Wakefield Westgate',
					type: 'origin',
					icon: map_icon_origin,
					position: {
						lat: 53.6831231,
						lng: -1.5081666,
					},
				},
				{
					station: 'Doncaster',
					type: 'pass-through',
					icon: map_icon_numbered_1,
					position: {
						lat: 53.5221776,
						lng: -1.1414691,
					},
				},
				{
					station: 'Grantham',
					type: 'pass-through',
					icon: map_icon_numbered_2,
					position: {
						lat: 52.9066102,
						lng: -0.6450912,
					},
				},
				{
					station: 'Stevenage',
					type: 'pass-through',
					icon: map_icon_numbered_3,
					position: {
						lat: 51.90169,
						lng: -0.20709,
					},
				},
				{
					station: 'London King\'s Cross',
					type: 'destination',
					icon: map_icon_destination,
					position: {
						lat: 51.5348549,
						lng: -0.1343696,
					},
				},
			];

			const google = await gmapsInit();
			const args = {
				zoom: 6,
				center: new google.maps.LatLng(54.2314991, -13.4289225),
				mapTypeId: google.maps.MapTypeId.ROADMAP,
				disableDefaultUI: true
			};
			const map = new google.maps.Map(this.$el, args);
			const bounds = new google.maps.LatLngBounds();

			map.markers = [];

			for (var i = journey.length - 1; i >= 0; i--) {

				const latlng = new google.maps.LatLng( journey[i].position.lat, journey[i].position.lng );

				if ( journey[i].type == 'origin' || journey[i].type == 'destination' ) {
					var map_icon = {
						url: journey[i].icon,
						size: new google.maps.Size(27, 40),
						origin: new google.maps.Point(0, 0),
						anchor: new google.maps.Point(13, 40),
						scaledSize: new google.maps.Size(27, 40)
					};
				} else {
					var map_icon = {
						url: journey[i].icon,
						size: new google.maps.Size(27, 27),
						origin: new google.maps.Point(0, 0),
						anchor: new google.maps.Point(13, 13),
						scaledSize: new google.maps.Size(27, 27)
					};
				}

				const marker = new google.maps.Marker({
					position	: latlng,
					map			: map,
					icon		: map_icon,
				});

				map.markers.push( marker );

				bounds.extend( latlng );

				map.fitBounds( bounds );

				const styles = [{"featureType":"all","elementType":"labels.text.fill","stylers":[{"saturation":36},{"color":"#000000"},{"lightness":40}]},{"featureType":"all","elementType":"labels.text.stroke","stylers":[{"visibility":"on"},{"color":"#000000"},{"lightness":16}]},{"featureType":"all","elementType":"labels.icon","stylers":[{"visibility":"off"}]},{"featureType":"administrative","elementType":"geometry.fill","stylers":[{"color":"#000000"},{"lightness":20}]},{"featureType":"administrative","elementType":"geometry.stroke","stylers":[{"color":"#000000"},{"lightness":17},{"weight":1.2}]},{"featureType":"landscape","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":20}]},{"featureType":"poi","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":21}]},{"featureType":"road.highway","elementType":"geometry.fill","stylers":[{"color":"#000000"},{"lightness":17}]},{"featureType":"road.highway","elementType":"geometry.stroke","stylers":[{"color":"#000000"},{"lightness":29},{"weight":.2}]},{"featureType":"road.arterial","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":18}]},{"featureType":"road.local","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":16}]},{"featureType":"transit","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":19}]},{"featureType":"water","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":17}]}];
				map.setOptions({styles: styles});

			}

		} catch (error) {
			console.error(error);
		}
	},
}
</script>

